@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);
@import url(https://fonts.googleapis.com/css?family=Saira+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bangers&display=swap);
body {
  cursor:url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'  width='40' height='48' viewport='0 0 100 100' style='fill:black;font-size:24px;'><text y='50%'>ðŸ‘»</text></svg>") 16 0,auto; /*emojicursor.app*/
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: black;
  background-image:
  radial-gradient(white, rgba(255,255,255,.2) 2px, transparent 40px),
  radial-gradient(white, rgba(255,255,255,.15) 1px, transparent 30px),
  radial-gradient(white, rgba(255,255,255,.1) 2px, transparent 40px),
  radial-gradient(rgba(255,255,255,.4), rgba(255,255,255,.1) 2px, transparent 30px);
  background-size: 550px 550px, 350px 350px, 250px 250px, 150px 150px;
  background-position: 0 0, 40px 60px, 130px 270px, 70px 100px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.calc {
  margin-left: 40%;
  margin-top: 5%;
  padding: 3em;
  display: flex;
  flex-flow: row wrap;
  align-content: space-between;
  width: 22em;
  height: 55em;
  background-color: silver;
  border-radius: 200px 200px 0 0;
  -webkit-mask-image: url(/calc/static/media/grit.83da5d64.png);
  mask-image: url(/calc/static/media/grit.83da5d64.png);
  border-right: 50px solid gray;
}

.title {
  font-family: 'Bangers', cursive;
  display: block;
  width: 100%;
  font-size: 3em;
}

.nButton, .input {
  vertical-align: middle;
  text-align: center;
  font-size: 30px;
  width: 2em;
  height: 2em;
  padding: 3%;
  margin-right: .3em;
  margin-left: .3em;
  border: 3px solid rgba(0, 0, 0, .5);
  border-radius: 15px;
}

.input {
  font-family: 'Saira Condensed', sans-serif;
  background-color: rgba(255, 255, 255, .5);
}

.nButton {
  font-family: 'Press Start 2P', cursive;
  cursor: pointer;
}

.nButton:hover {
  transition: all .5s ease;
  -webkit-transition: all .5s ease;
  background-color: rgba(255, 255, 255, .3);
  border: 3px solid rgba(255, 255, 255, .1);
  font-weight: bold;
}

#current {
  width: 5.5em;
  overflow-wrap: break-word;
  overflow: scroll;
}

.past {
  color: white;
  font-family: 'Saira Condensed', sans-serif;
  position: absolute;
  margin-bottom: 0;
  height: 100%;
  width: 100%;
  font-size: 3em;
  overflow-wrap: break-word;
}

.past::after {
  content: "";
  background-image: url(/calc/static/media/grass.c2ed3ea3.bin);
  opacity: 0.5;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;
}

.past > h3 {
  font-family: 'Bangers', cursive;
}

